const hints = document.querySelectorAll('.hint');
const hideHint = () => {
	hints.forEach($hint => {
		$hint.classList.remove('is-open');
	});
}
hints.forEach($hint => {
	$hint.addEventListener('click', (e) => {
		e.preventDefault();
		hideHint();
		if (e.target.closest('.hint__close')) {
			$hint.classList.remove('is-open')
		} else {
			$hint.classList.add('is-open')
		}
	})
});
