import Swiper, { Navigation, Pagination, Autoplay } from 'swiper';
const slidersEls = document.querySelectorAll('.s-slider-desc__swiper');
if (slidersEls.length) {
	slidersEls.forEach($slider => {
		let slider = new Swiper($slider, {
			modules: [Navigation, Pagination, Autoplay],
			loop: true,
			slidesPerView: 1,
			loopedSlides: 2,
			navigation: {
				nextEl: '.s-slider-desc__swiper-button-next',
				prevEl: '.s-slider-desc__swiper-button-prev'
			},
			pagination: {
				el: '.s-slider-desc__swiper .swiper-pagination',
				clickable: true
			}
		});
		slider.slideNext();
		slider.slidePrev();
	});
}

